.footer {
  background-color: #141414;
  padding: 3rem 5rem;
  display: grid;
  margin-top: 10rem;
}
.footer .caja_footer {
  width: min(95%, 120rem);
  margin: 0 auto;
  font-size: 1.8rem;
  display: grid;
  gap: 3rem;
  margin-bottom: 10rem;
  justify-content: center;
}
.footer .caja_footer a {
  color: #b1b1b1;
}
.footer .caja_footer p {
  color: #ffffff;
}
.footer .caja_footer .bajocero {
  display: grid;
}
.footer .caja_footer .ofertas {
  margin-bottom: 2rem;
}
.footer .caja_footer .caja_imagen_footer {
  display: block;
  text-align: center;
  box-sizing: border-box;
}
.footer .caja_footer .caja_imagen_footer .imagen_footer {
  box-sizing: border-box;
  height: auto;
  max-width: 100%;
  width: 40rem;
}
.footer .caja_footer .caja_imagen_footer .imagen_footer p {
  margin: 0 auto;
  font-size: 1.5rem;
  box-sizing: border-box;
}

.notifications {
  position: fixed;
  top: 7rem;
  right: 20px;
  z-index: 1000;
}
@media (min-width: 768px) {
  .notifications {
    top: 9rem;
  }
}

.notifications :where(.toast, .column) {
  display: flex;
  align-items: center;
}

.notifications .toast {
  width: 400px;
  position: relative;
  overflow: hidden;
  list-style: none;
  border-radius: 4px;
  padding: 16px 17px;
  margin-bottom: 10px;
  background: var(--light);
  justify-content: space-between;
  animation: show_toast 0.3s ease forwards;
}

@keyframes show_toast {
  0% {
    transform: translateX(100%);
  }
  40% {
    transform: translateX(-5%);
  }
  80% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-10px);
  }
}
.notifications .toast.hide {
  animation: hide_toast 0.3s ease forwards;
}

@keyframes hide_toast {
  0% {
    transform: translateX(-10px);
  }
  40% {
    transform: translateX(0%);
  }
  80% {
    transform: translateX(-5%);
  }
  100% {
    transform: translateX(calc(100% + 20px));
  }
}
.toast::before {
  position: absolute;
  content: "";
  height: 3px;
  width: 100%;
  bottom: 0px;
  left: 0px;
  animation: progress 5s linear forwards;
}

@keyframes progress {
  100% {
    width: 0%;
  }
}
.toast.success::before, .btn#success {
  background: var(--success);
}

.toast.error::before, .btn#error {
  background: var(--error);
}

.toast.warning::before, .btn#warning {
  background: var(--warning);
}

.toast.info::before, .btn#info {
  background: var(--info);
}

.toast .column i {
  font-size: 1.75rem;
}

.toast.success .column i {
  color: var(--success);
}

.toast.error .column i {
  color: var(--error);
}

.toast.warning .column i {
  color: var(--warning);
}

.toast.info .column i {
  color: var(--info);
}

.toast .column span {
  font-size: 1.5rem;
  margin-left: 12px;
  color: black;
}

.toast i:last-child {
  color: #aeb0d7;
  cursor: pointer;
}

.toast i:last-child:hover {
  color: var(--dark);
}

.buttons .btn {
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 5px;
  color: var(--light);
  font-size: 1.2rem;
  padding: 10px 20px;
  border-radius: 4px;
}

@media screen and (max-width: 530px) {
  .notifications {
    width: 95%;
  }
  .notifications .toast {
    width: 100%;
    font-size: 1rem;
    margin-left: 20px;
  }
  .buttons .btn {
    margin: 0 1px;
    font-size: 1.1rem;
    padding: 8px 15px;
  }
}